// Variables
:root {
  --color-primary: #000;
  --color-secondary: #2d81d6;
  --color-on-primary: #fbfcfd;
  --color-preview-sidebar: #fafafa;
  --color-bg: #fff;
  --color-bg-header: #fbfcfd;
  --color-bg-preview: #fff;
  --color-bg-preview-sidebar: #111;
  --color-form-field-border: #d7dde1;
  --color-error: red;
  --color-grey: #4a4a4a;
  --font: 'Open Sans', sans-serif;
}

// Reset styles
*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

// Base styles
body {
  font-family: var(--font);
  line-height: 1.5;
  color: var(--color-primary);
  background-color: var(--color-bg);
}

button,
input,
select,
textarea {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  text-align: left;
  color: inherit;
  background-color: inherit;
  border: none;
}
